igcaption, footer, header, hgroup,\r\nmenu, nav, output, ruby, section, summary,\r\ntime, mark, audio, video {\r\n\tmargin: 0;\r\n\tpadding: 0;\r\n\tborder: 0;\r\n\tfont-size: 100%;\r\n\tfont: inherit;\r\n\tvertical-align: baseline;\r\n}\r\n/* HTML5 display-role reset for older browsers */\r\narticle, aside, details, figcaption, figure,\r\nfooter, header, hgroup, menu, nav, section {\r\n\tdisplay: block;\r\n}\r\nbody {\r\n\tline-height: 1;\r\n}\r\nol, ul {\r\n\tlist-style: none;\r\n}\r\nblockquote, q {\r\n\tquotes: none;\r\n}\r\nblockquote:before, blockquote:after,\r\nq:before, q:after {\r\n\tcontent: '';\r\n\tcontent: none;\r\n}\r\ntable {\r\n\tborder-collapse: collapse;\r\n\tborder-spacing: 0;\r\n}\r\n\r\n/* Box sizing rules */\r\n*,\r\n*::before,\r\n*::after {\r\n  box-sizing: border-box;\r\n}\r\n\r\n/* Inherit fonts for inputs and buttons */\r\ninput,\r\nbutton,\r\ntextarea,\r\nselect {\r\n  font: inherit;\r\n}\r\n","/*\r\n  Space\r\n  getThemeProperty(layoutSpace) variable is defined in _fw-variables.scss\r\n  Used for consistent margins and gutters throughout the defined content\r\n*/\r\n.fw-space,\r\n.space{\r\n  padding: getThemeProperty(layoutSpace);\r\n  &.double{\r\n    padding: getThemeProperty(layoutSpace) * 2;\r\n  }\r\n  &.triple{\r\n    padding: getThemeProperty(layoutSpace) * 3;\r\n  }\r\n  &.one-forth{\r\n    padding: getThemeProperty(layoutSpace) * 0.25;\r\n  }\r\n  &.half{\r\n    padding: getThemeProperty(layoutSpace) / 2;\r\n  }\r\n  &.three-forths{\r\n    padding: getThemeProperty(layoutSpace) * 0.75;\r\n  }\r\n\t&.noleft{\r\n\t\tpadding-left: 0;\r\n\t}\r\n\t&.noright{\r\n\t\tpadding-right: 0;\r\n\t}\r\n\t&.halfleft{\r\n\t\tpadding-left: getThemeProperty(layoutSpace) / 2;\r\n\t}\r\n\t&.halfright{\r\n\t\tpadding-right: getThemeProperty(layoutSpace) / 2;\r\n\t}\r\n\t&.notop{\r\n\t\tpadding-top: 0;\r\n\t}\r\n\t&.nobottom{\r\n\t\tpadding-bottom: 0;\r\n\t}\r\n\t&.halftop{\r\n\t\tpadding-top: getThemeProperty(layoutSpace) / 2;\r\n\t}\r\n\t&.halfbottom{\r\n\t\tpadding-bottom: getThemeProperty(layoutSpace) / 2;\r\n\t}\r\n}\r\n","/*\r\n  Theme Properties\r\n*/\r\n$default-theme-properties: (\r\n  // Colors\r\n  primaryColor: var(--primaryColor, #999999),\r\n  secondaryColor: var(--secondaryColor, #000000),\r\n  tertiaryColor: var(--tertiaryColor, #cccccc),\r\n  backgroundColorLight: var(--backgroundColorLight, #ffffff),\r\n  backgroundColorDark: var(--backgroundColorDark, #333333),\r\n  textColorLight: var(--textColorLight, #ffffff),\r\n  textColorDark: var(--textColorDark, #333333),\r\n\r\n  // Fonts\r\n  fontHeader: var(--fontHeader, \"Montserrat\"),\r\n  fontText: var(--fontText, \"Roboto\"),\r\n  fontIcon: var(--fontIcon, \"Font Awesome 5 Pro\"),\r\n\r\n  // Layout\r\n  layoutSpace: 20px,\r\n  layoutContainerWdth: var(--layoutContainerWdth, 1366px),\r\n\r\n  // Breakpoints\r\n  breakpointDesktop: (\r\n    desktop: 1366px\r\n  ),\r\n  breakpointResponsive: (\r\n    desktopsmall: 1366px,\r\n    tablet: 1024px,\r\n    phone : 600px\r\n  )\r\n);\r\n\r\n/*\r\n  getThemeProperty\r\n  Returns the property value requested, either with the $theme-properties or the passed in $properties\r\n  - $key - The map element to lookup\r\n  - $properties - a map of properties to use instead of the default $theme-properties\r\n  - ex:\r\n    `color: getThemeProperty(primaryColor)`;\r\n*/\r\n@function getThemeProperty($key, $properties:()) {\r\n  @if(length($properties) > 0){ // Check to see if any properties were passed in\r\n    @if(map-get($properties, $key)){ // Check if the key exists in the properties\r\n      @return map-get($properties, $key);\r\n    }@else{\r\n      @return getThemeProperty($key); // Fall back to the theme properties\r\n    }\r\n  }@else{\r\n    @if(map-get($theme-properties, $key)){\r\n      @return map-get($theme-properties, $key);\r\n    }@else{\r\n      @return #00ff00; // The key was not found, so we return this color as an error\r\n    }\r\n  }\r\n}\r\n\r\n/*\r\n  Recursive Map Merge\r\n*/\r\n@function recursive-map-merge($parent-map, $child-map) {\r\n  $result: $parent-map;\r\n  @each $key, $value in $child-map {\r\n    @if (not map-has-key($result, $key)) or (type-of(map-get($result, $key)) != type-of($value)) or (not (type-of(map-get($result, $key)) == map and type-of($value) == map)) {\r\n      $result: map-merge($result, ($key: $value));\r\n    }\r\n    @else {\r\n      $result: map-merge($result, ($key: recursive-map-merge(map-get($result, $key), $value)));\r\n    }\r\n  }\r\n  @return $result;\r\n}\r\n\r\n// If $theme-properties is set, lets merge into our defaults\r\n$theme-properties: $default-theme-properties !default;\r\n@if($theme-properties){\r\n  $theme-properties: recursive-map-merge($default-theme-properties, $theme-properties);\r\n}\r\n","/*\r\n  Flex\r\n  Classes with default flex properties defined and inline flex helpers\r\n*/\r\n.flex-container{\r\n  display: -webkit-box;\r\n  display: -moz-box;\r\n  display: -ms-flexbox;\r\n  display: -webkit-flex;\r\n  display: flex;\r\n  flex-flow: row wrap;\r\n\tjustify-content: flex-start;\r\n  align-content: flex-start;\r\n}\r\n.flex-image {\r\n  width: 100%;\r\n  height: auto;\r\n}\r\n.flex-iframe{\r\n  width: 100%;\r\n  iframe{\r\n    width: 100%;\r\n  }\r\n}\r\n.flex-vertical-top {\r\n  align-items: flex-start;\r\n}\r\n.flex-vertical-center {\r\n  align-items: center;\r\n}\r\n.flex-vertical-bottom {\r\n  align-items: flex-end;\r\n}\r\n.flex-vertical-stretch {\r\n  align-items: stretch;\r\n}\r\n.flex-horizontal-left {\r\n  justify-content: flex-start;\r\n}\r\n.flex-horizontal-center {\r\n  justify-content: center;\r\n}\r\n.flex-horizontal-right {\r\n  justify-content: flex-end;\r\n}\r\n","/*\r\n  DEFAULT DESKTOP\r\n  If no breakpoints are defined, these will be the fallback width percentages\r\n*/\r\n.desktop-half{\r\n  width: 0.5%;\r\n  min-height: 1px;\r\n}\r\n@for $i from 1 through 100 {\r\n  .desktop-#{$i} {\r\n    width: round(percentage($i / 100));\r\n  }\r\n}\r\n.desktop-hide{\r\n  display: none;\r\n}\r\n.desktop-auto{\r\n  width: auto;\r\n}\r\n.desktop-show{\r\n  display: inherit;\r\n}\r\n/*\r\n  DESKTOP BREAKPOINTS\r\n  Loop through each desktop defined breakpoint\r\n*/\r\n@each $label, $breakpoint in getThemeProperty(breakpointDesktop){\r\n  @media screen and (min-width: $breakpoint) {\r\n    .#{$label}-half{\r\n      width: 0.5%;\r\n      min-height: 1px;\r\n    }\r\n    @for $i from 1 through 100 {\r\n      .#{$label}-#{$i} {\r\n        width: round(percentage($i / 100));\r\n      }\r\n    }\r\n    .#{$label}-auto{\r\n      width: auto;\r\n    }\r\n    .#{$label}-hide{\r\n      display: none;\r\n    }\r\n    .#{$label}-show{\r\n      display: inherit;\r\n    }\r\n  }\r\n}\r\n/*\r\n  RESPONSIVE BREAKPOINTS\r\n  Simply out puts classes with a width set from 1-100 for each breakpoint defined.\r\n*/\r\n@each $label, $breakpoint in getThemeProperty(breakpointResponsive){\r\n  @media only screen and (max-width: $breakpoint) {\r\n    .#{$label}-half{\r\n      width: 0.5%;\r\n      min-height: 1px;\r\n    }\r\n    @for $i from 1 through 100 {\r\n      .#{$label}-#{$i} {\r\n        width: round(percentage($i / 100));\r\n      }\r\n    }\r\n    .#{$label}-auto{\r\n      width: auto;\r\n    }\r\n    .#{$label}-hide{\r\n      display: none;\r\n    }\r\n    .#{$label}-show{\r\n      display: inherit;\r\n    }\r\n    /* Flex Container Direction */\r\n    .flex-container{\r\n      &.#{$label}-ltr{\r\n        flex-direction: row;\r\n      }\r\n      &.#{$label}-rtl{\r\n        flex-direction: row-reverse;\r\n      }\r\n      &.#{$label}-ttb{\r\n        flex-direction: column;\r\n      }\r\n      &.#{$label}-btt{\r\n        flex-direction: column-reverse;\r\n      }\r\n    }\r\n    /* SPACING RESET */\r\n    .space{\r\n      &.#{$label}-space-reset{\r\n        padding: getThemeProperty(layoutSpace);\r\n      }\r\n    }\r\n  }\r\n}\r\n\r\n","/*\r\n These are a part of the piccalil modern reset file\r\n  - https://piccalil.li/blog/a-modern-css-reset/\r\n*/\r\n\r\n/* 