adding-y: 15px,\r\n  hamburger-layer-width: 40px,\r\n  hamburger-layer-height: 4px,\r\n  hamburger-layer-spacing: 6px,\r\n  hamburger-layer-color: #000000,\r\n  hamburger-layer-border-radius: 4px,\r\n  hamburger-hover-opacity: 0.7,\r\n  hamburger-active-layer-color: #000000,\r\n  hamburger-active-hover-opacity: 0.7,\r\n);\r\n// If $component-hamburger-properties is set, lets merge into our defaults\r\n$component-hamburger-properties: $default-component-hamburger-properties !default;\r\n@if($component-hamburger-properties){\r\n  $component-hamburger-properties: map-merge($default-component-hamburger-properties, $component-hamburger-properties);\r\n}\r\n\r\n/*!\r\n * Hamburgers\r\n * @description Tasty CSS-animated hamburgers\r\n * @author Jonathan Suh @jonsuh\r\n * @site https://jonsuh.com/hamburgers\r\n * @link https://github.com/jonsuh/hamburgers\r\n */\r\n\r\n// Settings\r\n// ==================================================\r\n$hamburger-padding-x           : getThemeProperty(hamburger-padding-x, $component-hamburger-properties) !default;\r\n$hamburger-padding-y           : getThemeProperty(hamburger-padding-y, $component-hamburger-properties) !default;\r\n$hamburger-layer-width         : getThemeProperty(hamburger-layer-width, $component-hamburger-properties) !default;\r\n$hamburger-layer-height        : getThemeProperty(hamburger-layer-height, $component-hamburger-properties) !default;\r\n$hamburger-layer-spacing       : getThemeProperty(hamburger-layer-spacing , $component-hamburger-properties) !default;\r\n$hamburger-layer-color         : getThemeProperty(hamburger-layer-color , $component-hamburger-properties) !default;\r\n$hamburger-layer-border-radius : getThemeProperty(hamburger-layer-border-radius, $component-hamburger-properties) !default;\r\n$hamburger-hover-opacity       : getThemeProperty(hamburger-hover-opacity, $component-hamburger-properties) !default;\r\n$hamburger-active-layer-color  : getThemeProperty(hamburger-active-layer-color, $component-hamburger-properties) !default;\r\n$hamburger-active-hover-opacity: getThemeProperty(hamburger-active-hover-opacity, $component-hamburger-properties) !default;\r\n\r\n// To use CSS filters as the hover effect instead of opacity,\r\n// set $hamburger-hover-use-filter as true and\r\n// change the value of $hamburger-hover-filter accordingly.\r\n$hamburger-hover-use-filter   : false !default;\r\n$hamburger-hover-filter       : opacity(50%) !default;\r\n$hamburger-active-hover-filter: $hamburger-hover-filter !default;\r\n\r\n// Types (Remove or comment out what you don’t need)\r\n// ==================================================\r\n$hamburger-types: (\r\n  3dx,\r\n  3dx-r,\r\n  3dy,\r\n  3dy-r,\r\n  3dxy,\r\n  3dxy-r,\r\n  arrow,\r\n  arrow-r,\r\n  arrowalt,\r\n  arrowalt-r,\r\n  arrowturn,\r\n  arrowturn-r,\r\n  boring,\r\n  collapse,\r\n  collapse-r,\r\n  elastic,\r\n  elastic-r,\r\n  emphatic,\r\n  emphatic-r,\r\n  minus,\r\n  slider,\r\n  slider-r,\r\n  spin,\r\n  spin-r,\r\n  spring,\r\n  spring-r,\r\n  stand,\r\n  stand-r,\r\n  squeeze,\r\n  vortex,\r\n  vortex-r\r\n) !default;\r\n\r\n// Base Hamburger (We need this)\r\n// ==================================================\r\n@import \"hamburgers/_sass/hamburgers/base\";\r\n\r\n// Hamburger types\r\n// ==================================================\r\n@import \"hamburgers/_sass/hamburgers/types/3dx\";\r\n@import \"hamburgers/_sass/hamburgers/types/3dx-r\";\r\n@import \"hamburgers/_sass/hamburgers/types/3dy\";\r\n@import \"hamburgers/_sass/hamburgers/types/3dy-r\";\r\n@import \"hamburgers/_sass/hamburgers/types/3dxy\";\r\n@import \"hamburgers/_sass/hamburgers/types/3dxy-r\";\r\n@import \"hamburgers/_sass/hamburgers/types/arrow\";\r\n@import \"hamburgers/_sass/hamburgers/types/arrow-r\";\r\n@import \"hamburgers/_sass/hamburgers/types/arrowalt\";\r\n@import \"hamburgers/_sass/hamburgers/types/arrowalt-r\";\r\n@import \"hamburgers/_sass/hamburgers/types/arrowturn\";\r\n@import \"hamburgers/_sass/hamburgers/types/arrowturn-r\";\r\n@import \"hamburgers/_sass/hamburgers/types/boring\";\r\n@import \"hamburgers/_sass/hamburgers/types/collapse\";\r\n@import \"hamburgers/_sass/hamburgers/types/collapse-r\";\r\n@import \"hamburgers/_sass/hamburgers/types/elastic\";\r\n@import \"hamburgers/_sass/hamburgers/types/elastic-r\";\r\n@import \"hamburgers/_sass/hamburgers/types/emphatic\";\r\n@import \"hamburgers/_sass/hamburgers/types/emphatic-r\";\r\n@import \"hamburgers/_sass/hamburgers/types/minus\";\r\n@import \"hamburgers/_sass/hamburgers/types/slider\";\r\n@import \"hamburgers/_sass/hamburgers/types/slider-r\";\r\n@import \"hamburgers/_sass/hamburgers/types/spin\";\r\n@import \"hamburgers/_sass/hamburgers/types/spin-r\";\r\n@import \"hamburgers/_sass/hamburgers/types/spring\";\r\n@import \"hamburgers/_sass/hamburgers/types/spring-r\";\r\n@import \"hamburgers/_sass/hamburgers/types/stand\";\r\n@import \"hamburgers/_sass/hamburgers/types/stand-r\";\r\n@import \"hamburgers/_sass/hamburgers/types/squeeze\";\r\n@import \"hamburgers/_sass/hamburgers/types/vortex\";\r\n@import \"hamburgers/_sass/hamburgers/types/vortex-r\";\r\n","// Hamburger\n// ==================================================\n.hamburger {\n  padding: $hamburger-padding-y $hamburger-padding-x;\n  display: inline-block;\n  cursor: pointer;\n\n  transition-property: opacity, filter;\n  transition-duration: 0.15s;\n  transition-timing-function: linear;\n\n  // Normalize (<button>)\n  font: inherit;\n  color: inherit;\n  text-transform: none;\n  background-color: transparent;\n  border: 0;\n  margin: 0;\n  overflow: visible;\n\n  &:hover {\n    @if $hamburger-hover-use-filter == true {\n      filter: $hamburger-hover-filter;\n    }\n    @else {\n      opacity: $hamburger-hover-opacity;\n    }\n  }\n\n  &.is-active {\n    &:hover {\n      @if $hamburger-hover-use-filter == true {\n        filter: $hamburger-active-hover-filter;\n      }\n      @else {\n        opacity: $hamburger-active-hover-opacity;\n      }\n    }\n\n